    padding: 0;
    margin: 0;
  }
   
  body {
    font-family: 'Poppins', sans-serif;
    background-color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }
   
   /* Existing CSS */

/* Tautan Skip to Content */
.skip-content {
  position: absolute;
  top: -40px;
  left: 10px;
  background-color: #db0000;
  color: white;
  padding: 8px 12px;
  text-decoration: none;
  z-index: 100;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.skip-content:focus {
  top: 0;
  opacity: 1;
}

  /*
    AppBar
  */
   
  .app-bar {
    padding: 8px 16px;
    background-color: white;
    display: grid;
    grid-template-columns: auto 1fr auto;
    gap: 10px;
    position: fixed;
    top: 0;
    z-index: 99;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
    width: 100%;
  }
   
  .app-bar .app-bar__menu {
    display: flex;
    align-items: center;
  }
   
  .app-bar .app-bar__menu button {
    background-color: transparent;
    border: none;
    font-size: 18px;
    padding: 8px;
    cursor: pointer;
    min-width: 44px; 
    min-height: 44px;

  }
   
  .app-bar .app-bar__brand {
    display: flex;
    align-items: center;
  }
   
  .app-bar .app-bar__brand h1 {
    color:#333;
    font-size: 22px;
    user-select: none;
  }
   
  .app-bar .app-bar__navigation {
    position: absolute;
    top: 60px;
    right: -240px; 
    width: 200px;
    padding: 8px;
    background-color: white;
    overflow: hidden;
}

.app-bar .app-bar__navigation.open {
    right: 0; 
}
  
.app-bar .app-bar__navigation ul li a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: black;
  width: 44px;
  height: 44px;
  justify-content: space-between;
  margin: 20px 50px; 
  box-sizing: border-box; 
}

.app-bar .app-bar__navigation ul li a i {
  margin: 10px; 
}



.app-bar .app-bar__navigation ul li a:hover {
  background-color: #f0f0f0; 
}


    
   
  /*
    Main Content
  */
   
  main {
    display: flex;
    padding: 32px;
    flex: 1;
    
  }
   
  .content {
     width: 100%;
    height: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    overflow: hidden;
  }
   
  .content .content__heading {
    font-weight: normal;
    margin-bottom: 30px;
    text-align: center;
  }

  .content-head {
    text-align: center;
    font-weight: normal;
  }

   
   

   
  /*
    resto Item
  */
   
  .resto-item {
    width: 100%;
    height: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    overflow: hidden;
  }
   
  .resto-item__header {
    position: relative;
  }

  .resto-item-image {
    width: 100%;
    height: 200px;
    object-fit: cover; 
  }

   
  .resto-item .resto-item__header .resto-item__header__rating {
    position: absolute;
    padding: 8px;
    bottom: 20px;
    left: 0;
    display: inline-block;
    background-color: black;
    color: white;
  }
   
  .resto-item .resto-item__header .resto-item__header__rating .resto-item__header__rating__score {
    margin-left: 10px;
  }
   
  .resto-item-content {
    padding: 16px;
  }

  .resto-item-content .resto-title a {
    text-decoration: none;
    white-space: nowrap;
    font-size: 22px;
    color: #2C3333;
    display: inline-block; 
    align-items: center; 
    justify-content: center; 
    width: 44px; 
    height: 44px; 
  }
   
  .resto-item .resto-item-content h5 {
    margin: 0 0 10px 0;
    color:  red;
  }
   
   
  .resto-item .resto-item-content p {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4; 
    -webkit-box-orient: vertical;
    color:  #2C3333;
  }

  .resto-item-detail {
    width: 100%;
    height: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    overflow: hidden;
    margin-top: 20px; 
  }
  
  
  .resto-detail {
    display: flex; 
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }

  .resto-detail h1 {
    color: #2C3333;
    margin-top: 15px;
    margin-bottom: 5px;
    text-align: center;
  }
  
  .resto-detail .resto-poster {
    width: 80%;
    height: 400px;
    object-fit: cover;
    border-radius: 8px;
    margin: 0 auto; 
  }
  
  .resto-info h2 {
    color: #2C3333;
    margin-top: 15px;
    margin-bottom: 5px;
    text-align: center;
  }

  .detail-section p {
    text-align: justify;
  }
  
  .detail-section h3 {
    color: red;
    margin-bottom: 5px;
    margin-top: 5px;
  }
  


  /* Style for customer reviews */
  .resto-detail .resto-info .detail-section ul {
    padding: 0;
    
  }

  .resto-detail .resto-info .detail-section li {
    margin-bottom: 10px;
    padding: 8px;
    border-radius: 10px;
    background-color: #f0f0f0;
    display: block;
  }

  .resto-detail .resto-info .detail-section li span {
    font-weight: bold;
    display: block; 
    margin-bottom: 5px; 
  }

  .resto-detail .resto-info .detail-section li:nth-child(odd) {
    background-color: #e0e0e0;
  }


  /*
    Footer
  */
   
  footer {
    padding: 16px;
  }
   
  footer p {
    text-align: center;
    color: #aaaaaa;
  }
   
  footer p a {
    color: #db0000;
    text-decoration: none;
  }

/*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

#reviewFormContainer {
  max-width: 600px;
  margin: auto;
  padding: 20px;
  background-color: #333;
  color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
}

.restaurant__name {
  color: #ffffff;
}

.restaurant__info {
  margin-top: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
  color: #ffffff;
}


input,
textarea {
  width: 100%;
  margin-bottom: 10px;
  box-sizing: border-box;
  border: 1px solid #555555;
  border-radius: 4px;
  display: inline-block;
  min-height: 44px;
  min-width: 44px;
  background-color: #ffffff;
  color: #333333;
}

.btnsubmit {
  background-color: #4caf50;
  color: white;
  padding: 10px 15px;
  min-height: 44px;
  display: inline-block;
  min-width: 44px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,SAAS;EACX;;EAEA;IACE,kCAAkC;IAClC,uBAAuB;IACvB,iBAAiB;IACjB,aAAa;IACb,sBAAsB;EACxB;;GAEC,iBAAiB;;AAEpB,2BAA2B;AAC3B;EACE,kBAAkB;EAClB,UAAU;EACV,UAAU;EACV,yBAAyB;EACzB,YAAY;EACZ,iBAAiB;EACjB,qBAAqB;EACrB,YAAY;EACZ,UAAU;EACV,oCAAoC;AACtC;;AAEA;EACE,MAAM;EACN,UAAU;AACZ;;EAEE;;GAEC;;EAED;IACE,iBAAiB;IACjB,uBAAuB;IACvB,aAAa;IACb,oCAAoC;IACpC,SAAS;IACT,eAAe;IACf,MAAM;IACN,WAAW;IACX,0CAA0C;IAC1C,WAAW;EACb;;EAEA;IACE,aAAa;IACb,mBAAmB;EACrB;;EAEA;IACE,6BAA6B;IAC7B,YAAY;IACZ,eAAe;IACf,YAAY;IACZ,eAAe;IACf,eAAe;IACf,gBAAgB;;EAElB;;EAEA;IACE,aAAa;IACb,mBAAmB;EACrB;;EAEA;IACE,UAAU;IACV,eAAe;IACf,iBAAiB;EACnB;;EAEA;IACE,kBAAkB;IAClB,SAAS;IACT,aAAa;IACb,YAAY;IACZ,YAAY;IACZ,uBAAuB;IACvB,gBAAgB;AACpB;;AAEA;IACI,QAAQ;AACZ;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,qBAAqB;EACrB,YAAY;EACZ,WAAW;EACX,YAAY;EACZ,8BAA8B;EAC9B,iBAAiB;EACjB,sBAAsB;AACxB;;AAEA;EACE,YAAY;AACd;;;;AAIA;EACE,yBAAyB;AAC3B;;;;;EAKE;;GAEC;;EAED;IACE,aAAa;IACb,aAAa;IACb,OAAO;;EAET;;EAEA;KACG,WAAW;IACZ,YAAY;IACZ,0CAA0C;IAC1C,kBAAkB;IAClB,gBAAgB;EAClB;;EAEA;IACE,mBAAmB;IACnB,mBAAmB;IACnB,kBAAkB;EACpB;;EAEA;IACE,kBAAkB;IAClB,mBAAmB;EACrB;;;;;;EAMA;;GAEC;;EAED;IACE,WAAW;IACX,YAAY;IACZ,0CAA0C;IAC1C,kBAAkB;IAClB,gBAAgB;EAClB;;EAEA;IACE,kBAAkB;EACpB;;EAEA;IACE,WAAW;IACX,aAAa;IACb,iBAAiB;EACnB;;;EAGA;IACE,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,OAAO;IACP,qBAAqB;IACrB,uBAAuB;IACvB,YAAY;EACd;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,qBAAqB;IACrB,mBAAmB;IACnB,eAAe;IACf,cAAc;IACd,qBAAqB;IACrB,mBAAmB;IACnB,uBAAuB;IACvB,WAAW;IACX,YAAY;EACd;;EAEA;IACE,kBAAkB;IAClB,WAAW;EACb;;;EAGA;IACE,gBAAgB;IAChB,uBAAuB;IACvB,oBAAoB;IACpB,qBAAqB;IACrB,4BAA4B;IAC5B,eAAe;EACjB;;EAEA;IACE,WAAW;IACX,YAAY;IACZ,0CAA0C;IAC1C,kBAAkB;IAClB,gBAAgB;IAChB,gBAAgB;EAClB;;;EAGA;IACE,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;EACf;;EAEA;IACE,cAAc;IACd,gBAAgB;IAChB,kBAAkB;IAClB,kBAAkB;EACpB;;EAEA;IACE,UAAU;IACV,aAAa;IACb,iBAAiB;IACjB,kBAAkB;IAClB,cAAc;EAChB;;EA